			<h2></h2>
			<p></p>
			<p>2014610</p>
			<p>20</p>
			<p>19934040</p>
			<p></p>
			<p>20052013</p>
		</div>
		<a href="javascript:;" class="introduce__a">
			<span @click="more">{{type}}</span>
			<i :class="`${type === '' ? 'up' : 'down'}`"></i>
		</a>
	</section>
	
</template>

<script>
	export default{
		name:'introduce',
		data(){return {
