		}},
		components:{},
		mounted(){},
		updated(){},
		methods:{
			more(){
				let con = document.querySelector('.introduce__content');
				if(this.type==''){
					this.type = '';
					con.style.height = 'auto';
				}else{
					this.type = '';
					con.style.height = '4.4rem';
				}
			}
		}
		
	}
</script>

